
body{
    transition: background 0.5s;
}
.body-background{
    position: fixed;
    z-index: -1;
    left: 0;
    top:0;
    height: 100%;
    width: 100%;
    background-size: cover;
    opacity: 0;
    transition: opacity 0.2s linear,background 0.2s;
}
.background-show{
    opacity: 1.0;
}
@media (max-width: 500px){
.background-show{
        display: none;
}
.site-title{
        color:currentColor!important;
}
}
.navi{
    width: 100%;     
    position: relative;
    transition: all 1s;
    top:0;
}
.navi-list{
    width: 100%;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;        
    font-family:'JuzhenFang8dc9dfc505259';
}
.navi-list a{
    text-decoration: none;
}
.navi-list a,.navi-list a:active,.navi-list a:visited{
    color: currentColor;
}
.navi-item{
    width: auto;
    -ms-flex:auto;
        flex:auto;
    transition: flex 1s,opacity 1s;
    transition: flex 1s,opacity 1s,-ms-flex 1s;
}
.navi-is-bar{
    font-size: 24px;
}
.navi-is-menu{
    color:white;
    top: 35%;
    background-color: red;
    font-size: 36px;
}
.route-active{
    color:#f20c00!important;
}
.hide-return{
    width:0;
    height:0;
    -ms-flex:none;
        flex:none;
    opacity: 0;
    transform: translateX(-100px)
}



